@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap" rel="stylesheet);
body {
  margin: 0;
  background-image:  url(/App_Weather/static/media/weather.90e24d62.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  width: 100vw;
  height: 100vh;
  overflow: hidden;
}
.App{
    width: 700px;
    height: 100px;
    margin-top: 30vh;
    margin-left: auto;
    margin-right: auto;
    font-family: 'Roboto', sans-serif;
}

@media(orientation:portrait) and (max-width:1024px){
    .App{
        margin-top: 20vh;
        width: 850px;
    }    
}

@media(orientation:portrait) and (max-width:768px){
    .App{
        width: 650px;
    }
}

@media(orientation:portrait) and (max-width:414px){
    .App{
        margin-top: 15vh;
        width: 390px;
    }
}

@media(orientation:portrait) and (max-width:375px){
    .App{
        width: 330px;
    }
}

@media(orientation:portrait) and (max-width:280px){
    .App{
        width: 250px;
    }
}

/*orientation landscape*/
@media(orientation:landscape) and (max-width:1366px){
    .App{
        margin-top: 12vh;
        width: 800px;
    }
}

@media(orientation:landscape) and (max-width:1024px){
    .App{
        margin-top: 10vh;
    }
}

@media(orientation:landscape) and (max-width:823px){
    .App{
        width: 600px;
    }
}

@media(orientation:landscape) and (max-width:640px){
    .App{
        width: 500px;
    }
}
input{
    display: block;
    margin: 0 auto;
    padding: 5px;
    width: 80%;
    height: 50px;
    font-size: 40px;
    text-transform: uppercase;
    font-weight: bold;
    background-color: rgba(49, 125, 211, 0.4);
    border-radius: 3em;
}

@media(orientation:portrait) and (max-width:1024px){
    input{
        height: 80px;
        font-size: 50px;
    }
}

@media(orientation:portrait) and (max-width:768px){
    input{
        height: 50px;
        font-size: 40px;
    }
}

@media(orientation:portrait) and (max-width:414px){
    input{
        height: 40px;
        font-size: 30px;
    }
}

@media(orientation:portrait) and (max-width:375px){
    input{
        font-size: 25px;
    }
}

@media(orientation:portrait) and (max-width:280px){
    input{
        font-size: 23px;
        height: 30px;
    }
}

/*orientation landscape*/
@media(orientation:landscape) and (max-width:1366px){
    input{
        font-size: 60px;
        height: 80px;
    }
}

@media(orientation:landscape) and (max-width:1024px){
    input{
        font-size: 50px;
        height: 70px;
    }
}

@media(orientation:landscape) and (max-width:823px){
    input{
        font-size: 25px;
        height: 30px;
    }
}

@media(orientation:landscape) and (max-width:640px){
    input{
        font-size: 20px;
        height: 25px;
    }
}
.result{
    width: 100%;
    margin-top: 10px;
    border-radius: 3em;
    font-size: 30px;
    text-align: center;
}

.result div{
    opacity: 1;
    text-align: left;
    padding: 10px;
    border-radius: 2em;
    background-color: rgba(255, 255, 255, 0.4);
}

.result h1{
    font-size: 35px;
}

.result h2{
    font-size: 30px;
}

@media(orientation:portrait) and (max-width:1024px){
    .result{
        font-size: 50px;
    }
    .result h1{
        font-size: 45px;
    }
    .result h2{
        font-size: 40px;
    }
}

@media(orientation:portrait) and (max-width:768px){
    .result{
        font-size: 40px;
    }
    .result h1{
        font-size: 35px;
    }
    .result h2{
        font-size: 30px;
    }
}

@media(orientation:portrait) and (max-width:414px){
    .result{
        font-size: 30px;
    }
    .result h1{
        font-size: 30px;
    }
    .result h2{
        font-size: 25px;
    }
}

@media(orientation:portrait) and (max-width:375px){
    .result{
        font-size: 25px;
    }
    .result div{
        border-radius: 1em;
    }
    .result h1{
        font-size: 27px;
    }
    .result h2{
        font-size: 23px;
    }
}

@media(orientation:portrait) and (max-width:320px){
    .result h1{
        font-size: 23px;
    }
    .result h2{
        font-size: 18px;
    }
}

@media(orientation:portrait) and (max-width:280px){
    .result{
        font-size: 20px;
    }
    .result h1{
        font-size: 20px;
    }
}


/*orientation landscape*/
@media(orientation:landscape) and (max-width:1366px){
    .result{
        font-size: 50px;
    }
    .result h1{
        font-size: 43px;
    }
    .result h2{
        font-size: 35px;
    }
}

@media(orientation:landscape) and (max-width:1024px){
    .result{
        font-size: 40px;
    }
    .result h1{
        font-size: 40px;
    }
    .result h2{
        font-size: 30px;
    }
}

@media(orientation:landscape) and (max-width:823px){
    .result{
        font-size: 30px;
    }
    .result div{
        border-radius: 0.5em;
    }
    .result h1{
        font-size: 20px;
    }
    .result h2{
        font-size: 15px;
    }
}

@media(orientation:landscape) and (max-width:640px){
    .result{
        font-size: 30px;
    }
    .result div{
        border-radius: 0.5em;
        padding-top: 1px;
    }
    .result h1{
        font-size: 18px;
    }
    .result h2{
        font-size: 13px;
    }
}
